body {
  font-family: "Urbanist", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #222;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

h1,
.h1,
h2,
.h2 {
  font-size: 1.75rem;
  margin-bottom: 1.5rem;
}

h3 {
  font-size: 1.375rem;
  margin-bottom: 1rem;
}

.image-fill img {
  width: 100%;
  object-fit: cover;
  height: 100%;
}

p,
ol,
ul,
table {
  font-size: 1.125rem;
  line-height: 1.5;
  color: #666;
  margin-bottom: 1rem;
}

.bg-dark p,
.bg-dark ol,
.bg-dark ul,
.bg-dark table,
.bg-image p,
.bg-image ol,
.bg-image ul,
.bg-image table,
.bg-custom p,
.bg-custom ol,
.bg-custom ul,
.bg-custom table {
  color: white;
}

.bg-light p,
.bg-light ol,
.bg-light ul,
.bg-light table {
  color: #666;
}

.bg-dark,
.bg-image,
.bg-custom,
.bg-gradient {
  color: white;
}

.bg-dark {
  background-color: #222;
}

.bg-light {
  background-color: white;
  color: #222;
}

.bg-gradient {
  background: rgb(192, 80, 94);
  background: linear-gradient(308deg, rgb(192, 80, 94) 0%, rgb(244, 151, 11) 44%, rgb(244, 151, 11) 55%, rgb(192, 80, 94) 100%);
}

.button {
  border-radius: 500px;
  background-color: white;
  color: #222;
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  text-transform: uppercase;
  padding: 0.6rem 1.5rem;
  display: inline-block;
}

.home .header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.icon {
  width: 80px;
  margin-left: auto;
  margin-right: auto;
}

.layout .layout__block.bg-image {
  background-size: cover;
  background-repeat: no-repeat;
}
.layout .layout__block.bg-dark, .layout .layout__block.bg-image {
  color: white;
}

.menu {
  display: flex;
  display: none;
}
.menu > li > a {
  color: white;
}
.menu > li + li {
  margin-left: 2rem;
}
.menu .menu__dropdown {
  display: none;
}

.video-background {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
.video-background iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
}
@media (min-aspect-ratio: 16/9) {
  .video-background iframe {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 56.25vw;
  }
}
@media (max-aspect-ratio: 16/9) {
  .video-background iframe {
    /* width = 100 / (9 / 16) = 177.777777 */
    width: 177.78vh;
  }
}

.wysiwyg > *:last-child {
  margin-bottom: 0;
}
.wysiwyg--narrow-p p {
  max-width: 690px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}